<template>
  <div style="height:100%;">
    <div style="min-height:100%;">
      <div class="xa-panel xa-webkit-box">
        <circel-icon :size="48" color="#F8796F" icon="icon-iconfontnanshangjia"></circel-icon>
        <div style="padding:12px 14px;font-size:20px;">黄麒英</div>
      </div>
      <xa-cells>
        <xa-cell right title="推荐人数" desc="20"></xa-cell>
        <xa-cell right title="微信号" desc="wx_number"></xa-cell>
        <xa-cell right title="电话号码" desc="15522226666"></xa-cell>
        <xa-cell right title="身份证号" desc="462123199911224444"></xa-cell>
      </xa-cells>
      <div class="form-title">推荐人信息</div>
      <xa-cells>
        <xa-cell right title="推荐人" desc="黄飞鸿"></xa-cell>
        <xa-cell right title="联系方式" desc="15522226666"></xa-cell>
      </xa-cells>
      <div class="form-title">信息修改</div>
      <xa-cells>
        <xa-cell right arrow title="修改密码" link="/modify"></xa-cell>
      </xa-cells>
    </div>
    <xa-button @onclick="onBtnClick" style="display:block;padding:8px 10px;margin: 20px 10px 10px 10px;width:initial;" ripple="rgba(255,255,255,0.3)" :radius="6" width="100%" class="xa-bg-red xa-txt-white" event="onclick">退出登录</xa-button>
  </div>
</template>

<script>
import { getDemoList } from 'service/demo'
import { setDocumentTitle, getRandomColor } from 'utils'
import circelIcon from 'components/circelIcon.vue'

export default {
  name: 'demo-index',
  components: {circelIcon},
  data () {
    return {
      ramdomColor: getRandomColor()
    }
  },
  methods: {
    onBtnClick () {
      localStorage.removeItem('user_info')
      this.$router.push('/login')
    }
  },
  created () {
    setDocumentTitle('我的')
    getDemoList({a: 1, b: '', c: undefined, d: 0}).then(data => {
      console.log(data)
    }).catch((err) => {
      console.log(err)
    })
  }
}
</script>



