<template>
  <div class="xa-flex xa-webkit-vertical" style="background-color:white;">
    <div style="text-align:center;padding:0 30px;max-width: 640px;margin: 50px auto 0;">
      <div style="line-height:40px;font-size:20px;font-weight:bold;color:#F8796F;margin-bottom:18px;">登录</div>
      <input v-model="query.phone" class="xa-input login-input" type="text" placeholder="请输入登录手机号">
      <input v-model="query.password" class="xa-input login-input" type="password" placeholder="请输入密码">
      <xa-button @onclick="onBtnClick" style="padding:8px 10px;margin: 10px 0;" ripple="rgba(255,255,255,0.3)" :radius="6" width="100%" class="xa-bg-red xa-txt-white" event="onclick">登录</xa-button>
    </div>
  </div>
</template>

<script>
import { login } from 'service/member'
import { setStorage } from 'utils'
export default {
  data () {
    return {
      query: {
        phone: '',
        password: ''
      }
    }
  },
  methods: {
    onBtnClick () {
      if (!this.query.phone.trim()) {
        return this.$toast.show('请输入登录手机号', 3000)
      }
      if (!this.query.password.trim()) {
        return this.$toast.show('请输入登录密码', 3000)
      }
      this.$toast.show('请稍后...')
      login(this.query).then(data => {
        setStorage('user_info', data)
        this.$router.replace('/')
      }).catch(() => {
        setStorage('user_info', {})
        this.$router.replace('/')
        this.$toast.show('登录失败', 3000)
      })
    }
  },
  created () {

  }
}
</script>

<style scoped>
  .login-input{border: 1px solid #F8796F;height:40px;margin: 10px 0;border-radius:4px;padding: 0 10px;color:#444;}
</style>



